<template>
  <b-container class="bv-example-row mt-3">
    <b-row>
      <b-col>
        <h1 style="font-size: 60px"> </h1>
        <b-form style="font-size: 20px" @submit="onSubmit" @reset="onReset">
          <b-form-group
            id="id-group"
            label=":"
            label-for="id"
            description=" ."
          >
            <b-form-input
              size="lg"
              @blur="id_check"
              id="id"
              v-model="userInfo.id"
              type="text"
              required
              placeholder=" ..."
            ></b-form-input>
          </b-form-group>

          <b-form-group
            id="subject-group"
            label=":"
            label-for="password"
            description=" ."
          >
            <b-form-input
              size="lg"
              id="subject"
              v-model="userInfo.password"
              type="password"
              required
              placeholder=" ..."
            ></b-form-input>
          </b-form-group>

          <b-form-group id="content-group" label=":" label-for="name">
            <b-form-textarea
              size="lg"
              id="name"
              v-model="userInfo.name"
              placeholder=" ..."
              type="text"
              required
            ></b-form-textarea>
          </b-form-group>

          <b-form-group id="content-group" label=":" label-for="email">
            <b-form-textarea
              size="lg"
              @blur="email_check"
              id="email"
              v-model="userInfo.email"
              placeholder=" ..."
              type="email"
              required
            ></b-form-textarea>
          </b-form-group>

          <b-form-group
            id="content-group"
            label=" :"
            label-for="phone"
          >
            <b-form-textarea
              size="lg"
              id="phone"
              v-model="userInfo.phone"
              placeholder="  ..."
              type="text"
              required
            ></b-form-textarea>
          </b-form-group>

          <b-form-group id="content-group" label=":" label-for="address">
            <b-form-textarea
              size="lg"
              id="address"
              v-model="userInfo.address"
              placeholder=" ..."
              type="text"
              required
            ></b-form-textarea>
          </b-form-group>

          <b-button size="lg" type="submit" variant="primary" class="m-1"
            > </b-button
          >
          <b-button size="lg" type="reset" variant="danger" class="m-1"
            ></b-button
          >
        </b-form>
      </b-col>
    </b-row>
  </b-container>
</template>

<script>
import { memberRegister } from "@/api/member.js";
import axios from "axios";

export default {
  name: "MemberRegister",
  data() {
    return {
      userInfo: {
        id: "",
        password: "",
        name: "",
        email: "",
        phone: "",
        address: "",
      },
    };
  },
  created() {},
  methods: {
    onSubmit(event) {
      event.preventDefault();

      let err = true;
      let msg = "";
      !this.userInfo.id &&
        ((msg = " "), (err = false), this.$refs.id);
      err &&
        !this.userInfo.password &&
        ((msg = " "), (err = false), this.$refs.subject);
      err &&
        !this.userInfo.name &&
        ((msg = " "), (err = false), this.$refs.content);
      err &&
        !this.userInfo.email &&
        ((msg = " "), (err = false), this.$refs.content);
      err &&
        !this.userInfo.phone &&
        ((msg = "  "),
        (err = false),
        this.$refs.content);
      err &&
        !this.userInfo.address &&
        ((msg = " "), (err = false), this.$refs.content);

      if (!err) alert(msg);
      else this.registMember();
    },
    onReset(event) {
      event.preventDefault();
      this.userInfo.id = "";
      this.userInfo.password = "";
      this.userInfo.name = "";
      this.userInfo.email = "";
      this.userInfo.phone = "";
      this.userInfo.address = "";
      this.$router.push({ name: "boardList" });
    },
    id_check() {
      axios
        .get(
          `http://localhost:8080/happyhouse/member/check_id/${this.userInfo.id}`
        )
        .then((response) => {
          console.log(response);
          if (response.data.result > 0) {
            alert("  .");
          }
        })
        .catch((error) => {
          console.log(error);
        });
    },
    email_check() {
      axios
        .get(
          `http://localhost:8080/happyhouse/member/check_email/${this.userInfo.email}`
        )
        .then((response) => {
          console.log(response);
          if (response.data.result > 0) {
            alert("  ");
          }
        })
        .catch((error) => {
          console.log(error);
        });
    },
    registMember() {
      memberRegister(
        {
          id: this.userInfo.id,
          password: this.userInfo.password,
          name: this.userInfo.name,
          email: this.userInfo.email,
          phone: this.userInfo.phone,
          address: this.userInfo.address,
        },
        ({ data }) => {
          let msg = "   .";
          if (data.message === "success") {
            msg =
              " .     !";
          } else {
            msg = " .(   )";
          }
          alert(msg);
          this.$router.push({ name: "boardList" });
        },
        (error) => {
          console.log(error);
        }
      );
    },
  },
};
</script>

<style></style>
