<template>
  <div>
    <b-container>
      <b-row>
        <b-col>
          <h1 style="font-size: 60px"> </h1>
        </b-col>
      </b-row>
      <b-row style="font-size: 20px">
        <b-col>
          <b-form @submit="onSubmit" @reset="onReset">
            <b-form-group
              id="id-group"
              label=":"
              label-for="id"
              description=" ."
            >
              <b-form-input
                size="lg"
                id="id"
                v-model="id"
                type="text"
                required
                placeholder=" ..."
              ></b-form-input>
            </b-form-group>
            <b-form-group
              id="email-group"
              label=":"
              label-for="email"
              description=" ."
            >
              <b-form-input
                size="lg"
                id="email"
                type="text"
                v-model="email"
                required
                placeholder=" ..."
              ></b-form-input>
            </b-form-group>
            <b-button size="lg" type="submit" variant="primary" class="m-1"
              >  </b-button
            >
            <b-button size="lg" type="reset" variant="danger" class="m-1"
              ></b-button
            >
          </b-form>
        </b-col>
      </b-row>
    </b-container>
  </div>
</template>

<script>
import axios from "axios";

export default {
  data() {
    return {
      id: "",
      email: "",
    };
  },
  methods: {
    onSubmit(event) {
      event.preventDefault();

      axios
        .post("http://localhost:8080/happyhouse/member/find_pw", {
          id: this.id,
          email: this.email,
        })
        .then((response) => {
          console.log(response.data.result);
          let msg = "";
          if (response.data.result == 1) {
            msg = "  ";
          } else if (response.data.result == 2) {
            msg = "  ";
          } else if (response.data.result == 3) {
            msg = "  .";
          }
          alert(msg);
          this.$router.push({ name: "signIn" });
        })
        .catch((error) => {
          console.log(error);
        });
    },
    onReset() {
      this.$router.push({ name: "signIn" });
    },
  },
};
</script>

<style></style>
