<template>
  <b-row class="mb-1" style="font-size: 20px">
    <b-col style="text-align: left">
      <b-form @submit="onSubmit" @reset="onReset">
        <b-form-group
          id="id-group"
          label=":"
          label-for="id"
          description=" ."
        >
          <b-form-input
            size="lg"
            id="id"
            disabled
            v-model="article.id"
            type="text"
            required
            placeholder=" ..."
          ></b-form-input>
        </b-form-group>

        <b-form-group
          id="subject-group"
          label=":"
          label-for="subject"
          description=" ."
        >
          <b-form-input
            size="lg"
            id="subject"
            v-model="article.subject"
            type="text"
            required
            placeholder=" ..."
          ></b-form-input>
        </b-form-group>

        <b-form-group id="content-group" label=":" label-for="content">
          <b-form-textarea
            size="lg"
            id="content"
            v-model="article.content"
            placeholder=" ..."
            rows="10"
            max-rows="15"
          ></b-form-textarea>
        </b-form-group>
        <b-row align-h="end"
          ><b-button
            size="lg"
            type="submit"
            variant="primary"
            class="m-1"
            v-if="this.type === 'register'"
            ></b-button
          >
          <b-button size="lg" type="submit" variant="primary" class="m-1" v-else
            ></b-button
          >
          <b-button size="lg" type="reset" variant="danger" class="m-1"
            ></b-button
          ></b-row
        >
      </b-form>
    </b-col>
  </b-row>
</template>

<script>
import { writeArticle, getArticle, modifyArticle } from "@/api/board";
import { mapState } from "vuex";

const memberStore = "memberStore";

export default {
  name: "BoardInputItem",
  data() {
    return {
      article: {
        articleno: 0,
        id: "",
        subject: "",
        content: "",
      },
    };
  },
  props: {
    type: { type: String },
  },
  computed: {
    ...mapState(memberStore, ["userInfo"]),
  },
  created() {
    if (this.type === "modify") {
      getArticle(
        this.$route.params.articleno,
        ({ data }) => {
          // this.article.articleno = data.article.articleno;
          // this.article.id = data.article.id;
          // this.article.subject = data.article.subject;
          // this.article.content = data.article.content;
          this.article = data;
        },
        (error) => {
          console.log(error);
        }
      );
      this.isId = true;
    } else {
      this.article.id = this.userInfo.id;
    }
  },
  methods: {
    onSubmit(event) {
      event.preventDefault();

      let err = true;
      let msg = "";
      !this.article.id &&
        ((msg = " "), (err = false), this.$refs.id);
      err &&
        !this.article.subject &&
        ((msg = " "), (err = false), this.$refs.subject);
      err &&
        !this.article.content &&
        ((msg = " "), (err = false), this.$refs.content);

      if (!err) alert(msg);
      else
        this.type === "register" ? this.registArticle() : this.modifyArticle();
    },
    onReset(event) {
      event.preventDefault();
      this.article.articleno = 0;
      this.article.subject = "";
      this.article.content = "";
      this.$router.push({ name: "boardList" });
    },
    registArticle() {
      writeArticle(
        {
          id: this.article.id,
          subject: this.article.subject,
          content: this.article.content,
        },
        ({ data }) => {
          let msg = "   .";
          if (data === "success") {
            msg = " .";
          }
          alert(msg);
          this.moveList();
        },
        (error) => {
          console.log(error);
        }
      );
    },
    modifyArticle() {
      modifyArticle(
        {
          articleno: this.article.articleno,
          id: this.article.id,
          subject: this.article.subject,
          content: this.article.content,
        },
        ({ data }) => {
          let msg = "   .";
          if (data === "success") {
            msg = " .";
          }
          alert(msg);
          //  route /list .
          this.$router.push({ name: "boardList" });
        },
        (error) => {
          console.log(error);
        }
      );
    },
    moveList() {
      this.$router.push({ name: "boardList" });
    },
  },
};
</script>

