<template>
  <b-container class="bv-example-row mt-3">
    <b-row>
      <b-col>
        <h1 style="font-size: 60px"></h1>
      </b-col>
    </b-row>
    <b-row style="font-size: 20px">
      <b-col></b-col>
      <b-col cols="8">
        <b-card class="text-center mt-3" style="max-width: 40rem" align="left">
          <b-form class="text-left">
            <b-alert show variant="danger" v-if="isLoginError"
              >   .</b-alert
            >
            <b-form-group label=":" label-for="id">
              <b-form-input
                size="lg"
                id="id"
                v-model="user.id"
                required
                placeholder=" ...."
                @keyup.enter="confirm"
              ></b-form-input>
            </b-form-group>
            <b-form-group label=":" label-for="userpwd">
              <b-form-input
                size="lg"
                type="password"
                id="userpwd"
                v-model="user.password"
                required
                placeholder=" ...."
                @keyup.enter="confirm"
              ></b-form-input>
            </b-form-group>
            <b-row align-h="end">
              <b-button
                size="lg"
                type="button"
                variant="primary"
                class="m-1"
                @click="confirm"
                ></b-button
              >
              <b-button
                size="lg"
                type="button"
                variant="success"
                class="m-1"
                @click="movePage"
                ></b-button
              >
              <b-button
                size="lg"
                type="button"
                variant="info"
                class="m-1"
                @click="movePage2"
                > </b-button
              >
            </b-row>
          </b-form>
        </b-card>
      </b-col>
      <b-col></b-col>
    </b-row>
  </b-container>
</template>

<script>
import { mapState, mapActions } from "vuex";

const memberStore = "memberStore";

export default {
  name: "MemberLogin",
  data() {
    return {
      user: {
        id: null,
        password: null,
      },
    };
  },
  computed: {
    ...mapState(memberStore, ["isLogin", "isLoginError"]),
  },
  methods: {
    ...mapActions(memberStore, ["userConfirm", "getUserInfo"]),
    async confirm() {
      await this.userConfirm(this.user);
      let token = sessionStorage.getItem("access-token");
      if (this.isLogin) {
        await this.getUserInfo(token);

        this.$router.push({ name: "home" });
        setTimeout(function () {
          window.location.reload();
        }, 100);
      }
    },
    movePage() {
      this.$router.push({ name: "signup" });
    },
    movePage2() {
      this.$router.push({ name: "find_pw" });
    },
  },
};
</script>

<style></style>
